import React from "react";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import { Layout } from "./components/layout";
import { AboutMe, Projects, Home, ContactMe } from "./components/pages";
import { Typing } from "./components/scripts";
import "./App.css";
const dataPackage = require("../package.json");

let { homepage } = dataPackage;
console.log(homepage);
if (!homepage) {
  homepage = "";
}

function App() {
  return (
    <div>
      <Router>
        <Layout>
          <Switch>
            <Route exact path={homepage + "/"} component={Home}></Route>
            <Route path={homepage + "/About"} component={AboutMe}></Route>
            <Route path={homepage + "/Projects"} component={Projects}></Route>
            <Route path={homepage + "/Contact"} component={ContactMe}></Route>
          </Switch>
        </Layout>
      </Router>
      <Typing />
    </div>
  );
}

export default App;
